import React from 'react';
import '@google/model-viewer';



import predioGLB from '../../assets/building.glb';
import predioUSDZ from '../../assets/building.usdz';
import iconAR from '../../assets/ar.svg';



//<Link to="/interior"><button style={{ backgroundColor: "rgb(71, 71, 71)", borderRadius: "50%", border: "none", position: "absolute", top: "35%", right: "3%", width: "55px", height: "55px", cursor: "pointer", boxShadow: "2px 2px 5px #888888" }}><img src={iconINT} width="28px" alt="icon the interior" /></button></Link>
//<Link to="/maps"><button style={{ backgroundColor: "rgb(71, 71, 71)", borderRadius: "50%", border: "none", position: "absolute", top: "45%", right: "3%", width: "55px", height: "55px", cursor: "pointer", boxShadow: "2px 2px 5px #888888" }}><img src={iconMAP} width="28px" alt="icon the Map" /></button></Link>

export default function ModelView() {
    return (
        <div>
            
            <model-viewer
                src={predioGLB}
                ios-src={predioUSDZ}
                autoplay
                ar
                ar-modes="webxr scene-viewer quick-look"
                camera-controls
                alt="A 3D model of an build make by Dilis"
                auto-rotate
                data-js-focus-visible
            >
                <button slot="ar-button" style={{ backgroundColor: "#6e48aa", borderRadius: "50%", border: "none", position: "absolute", top: "15%", right: "3%", width: "55px", height: "55px", cursor: "pointer", boxShadow: "2px 2px 5px #888888" }}><img src={iconAR} width="28px" alt="icon the AR" /></button>
            </model-viewer>
        </div>
    )
}
